@charset "UTF-8";
.zoehis_tree{
  background-color: $FONTWHITE;
}
.zoehis_tree__empty_block{
  min-height: 60px;text-align: center;width: 100%;height: 100%;
}
.zoehis_tree__empty_text{
  font-size: $FONTMAINS;color: $MAINURGENT;
}
.zoehis_tree_node{
  white-space: nowrap;
}
.zoehis_tree_node_selected{
  color:$MAINCOLOR;
}
.zoe_tree_node__icon{
  font-size: $ICONFONTRIGHT;
}
.zoehis_tree_node__content{
  height: 28px; line-height: 28px;cursor: pointer;
  .z_open_form{
    &.is-leaf{
      &:before{
        visibility: hidden;
      }
    }
    &.expanded{
      &:before{
        content: "\e67a";
      }
    }
  }
}
.zoehis_tree__highlight_current .zoehis_tree_node.is-current>.zoehis_tree_node__content .zoehis_tree_node__label_item{
  color:$MAINCOLOR;
}







