.zoehis_button{
  display: inline-block; line-height: 1; white-space: nowrap; cursor: pointer; background: $MAINBG; border: 1px solid $MAINCOLOR; position: relative;
  -webkit-appearance: none; text-align: center; box-sizing: border-box; outline: none; margin: 0; -moz-user-select: none; min-width: 80px;
  -webkit-user-select: none; -ms-user-select: none; padding: 7px 25px; font-size: $FONTMAINS; border-radius: 15px; color: $FONTMAIN;
  &:focus{border: 2px $BOTTONACTIVE solid; padding: 6px 24px;
    &.zoehis_button_small{
      padding: 3px 14px;
    }
  }
  &:hover{background: $MAINCOLOR; color:$FONTWHITE;}
  &.zoehis_disabled{background: $MENUAIDE; color:$MAINNOEDITOR; border-color: $MENUAIDE; cursor: default;
    &:hover{background: $MENUAIDE; color:$MAINNOEDITOR; border-color: $MENUAIDE;}
  }
  + .zoehis_button{margin-left: 10px;}
  + .zoehis_button_features{
    margin-left: 7px;
    &:before{
      content: ""; position: absolute; height: 22px; border-right: 1px solid $MAINBORDERLINES; left: -7px; top: 50%; margin-top: -11px;
    }
    &.no_line:before{border:none;}
  }
  &.zoehis_button_small{
    border-radius: 12px; padding: 4px 15px;
  }
};
.zoehis_button_primary{
  background: $MAINCOLOR; color:$FONTWHITE;
  &:hover{background: $MAINNAV; color:$FONTWHITE;}
}
.zoehis_button_features{//特殊的按钮
  margin: 0 8px; border-radius: 0; border: none; padding: 0 7px; height: 48px; line-height: 48px;
  &:focus{color:$MAINCOLOR; background: $MAINBG; padding: 0 7px; border: none;}
  &:hover{background: $MAINMASK; color:$FONTMAIN;}
  &.has_icon{padding-left: 41px;}
  &.zoehis_disabled{background: $MAINBG; color:$FONTAUXILIARY;
    &:hover,&:focus{background:$MAINBG;}
    i{color:$FONTAUXILIARY;}
  }
  i{ font-size: $ICONFONTBTN; position: absolute; left: 7px; color: $MAINCOLOR;}
}
.zoehis_button_desc{
  font-size: $FONTMAINS;
}
.zoehis_button_keyboard{
  text-decoration: underline;  font-size: $FONTMAINS;
}
.zoehis_button_group{
  text-align: right;
}



