//input与textarea的公共部分
.ZoehisInputTextArea{
  font-size: $FONTSIZE; display: inline-block; text-indent: 0; position: relative; vertical-align: bottom; min-height: 24px;
}

.zoehis_input{
  @extend .ZoehisInputTextArea;
  &.zoehis_disabled{
    .zoehis_input_inner{border-bottom: none; background:$MAINBG;}
  }
  &.zoehis_over {
    .zoehis_input_icon.zoehis_close{color: $MAINCOLOR;}
    .zoehis_input_inner{border-bottom: 1px solid $MAINCOLOR;}
  }
  //为空时的线
  &.zoehis_line:before{
    content:""; border: none; height: 1px; width: 7px; background: #000; position: absolute; top: 50%; left: 9px;
  }
  .zoehis_input_inner{
    outline: 0; padding: 0 5px; margin: 0; border: none; height: 100%; min-height: 24px; width: 100%; box-sizing: border-box; border-bottom: 1px solid $MAINBORDERLINES;
    &::-webkit-input-placeholder {color: $MAINNOEDITOR;}
    &:-moz-placeholder {color: $MAINNOEDITOR;}
    &:-ms-input-placeholder {color: $MAINNOEDITOR;}
  }
  .zoehis_input_icon{
    position: absolute; right: 0; bottom: 0; top: 0; line-height: 24px; text-align: center; width: 20px; font-size:$ICONFONTNORMAL; color: $MAINCOLOR; cursor: pointer;
    + .zoehis_input_inner{padding-right: 20px;}
    &.zoehis_close{font-size: $FONTINSTRUCTIONS; color: $FONTMAIN;}
  }
}

//多行文本框
.zoehis_textarea{
  @extend .ZoehisInputTextArea;
  .zoehis_textarea_inner{
    resize: none;
    outline: 0; padding: 2px 10px 2px 5px; margin: 0; height: 100%; width: 100%; box-sizing: border-box; border: 1px solid $MAINBORDERLINES; line-height: 18px; vertical-align: bottom;
    &::-webkit-input-placeholder {color: $MAINNOEDITOR;}
    &:-moz-placeholder {color: $MAINNOEDITOR;}
    &:-ms-input-placeholder {color: $MAINNOEDITOR;}
    &:hover,&:focus{border: 1px solid $MAINCOLOR;}
    &.zoehis_disabled{background:$MAINBG;}

    //滚动条
    &::-webkit-scrollbar-thumb{ background:$MAINBORDERLINES;border-radius:3px;
      &:hover{background: $FONTRPTITLE; cursor: pointer;}
    }
    &::-webkit-scrollbar{width: 6px;}
  }
  .zoehis_textarea_icon{
    display: none;
    position: absolute; right: 2px; top: 2px; font-size:$FONTINSTRUCTIONS; @include transform(scale(0.6)); font-weight: bold; color: $MAINCOLOR; cursor: pointer;
  }
  &.zoehis_over{
    .zoehis_textarea_icon{
      display: block;
    }
    .zoehis_textarea_inner{border-color:$MAINCOLOR;}
  }
  &.zoehis_disabled{
    .zoehis_textarea_inner{ background:$MAINBG; border-color:$MAINBORDERLINES;}
  }
}




